<template>
  <div class="login-wrapper">
    <div class="bigtitle">Login to Agentily</div>
    <yd-cell-group>
        <yd-cell-item>
            <span slot="left">Username: </span>
            <yd-input slot="right" required v-model="username" max="20" placeholder="username"></yd-input>
        </yd-cell-item>
        <yd-cell-item>
            <span slot="left">Password: </span>
            <yd-input slot="right" type="password" v-model="password" placeholder="Password please"></yd-input>
        </yd-cell-item>
    </yd-cell-group>

    <yd-button-group>
        <yd-button size="large" @click.native="handleClick" type="primary">Login</yd-button>
    </yd-button-group>

    <yd-button-group>
        <yd-button size="large" @click.native="handleClick" type="primary">Login</yd-button>
    </yd-button-group>

            <yd-button @click.native="toastSuccess" size="large" type="danger">Toast->Success</yd-button>

    
  </div>
</template>

<script>
import { APIlogin } from '../../api/api'
import {Alert,Button,ButtonGroup} from 'vue-ydui'
import  cookie  from '../../static/js/cookie'
export default {
  components: {
    Button,
    ButtonGroup,
    Alert
  },
  data () {
    return {
      username:'woooms',
      password:'k51070520'
    }
  },
  created(){
      //进到这个页面就清除token
      cookie.delCookie('token');
      cookie.delCookie('username');
      //重新触发store
      //更新store数据
      // this.$store.dispatch('setInfo');
    },
  methods : {
      toastSuccess() {
                this.$dialog.toast({
                    mes: '鼠标不错哟！',
                    timeout: 1500,
                    icon: 'success'
                });
            },
    handleClick () {
      APIlogin({
        'username':this.username,
        'password': this.password
        }).then( res => {

          cookie.setCookie('username',this.username,60)
          cookie.setCookie('token',res.data.token,60)
                
          this.$dialog.toast({mes: 'Welcome back' + this.username})
          
          // this.$dialog.alert({mes: '执行了事件'})

          this.$router.push(this.$route.query.redirect || '/')

      }).catch( err => { 
          this.$dialog.alert({mes: err.response.data.non_field_errors['0']})
        })
    },
  
  }
  
}
</script>

<style lang="stylus" scoped>

</style>




